.container1 {
  width: 100%;
  height: 100%;
  background: linear-gradient(
    45deg,
    hsl(103, 86%, 71%),
    #4ae0c4e1
  ); /* Gradient background */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0); /* Shadow for depth */
  border-radius: 10px; /* Rounded corners */
  position: relative;
  overflow: hidden;
}

.container1::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(
      90deg,
      rgba(255, 255, 255, 0) 1px,
      transparent 1px
    ),
    linear-gradient(rgba(255, 255, 255, 0) 1px, transparent 1px);
  background-size: 20px 20px;
  pointer-events: none; /* Allow clicking through the pattern layer */
}


.app {
  display: flex;
  flex-direction: row-reverse; /* Arrange items side by side */
  align-items: center;
  justify-content: center;
  margin-top: 0px;
  height: 100vh;
  font-family: Arial, sans-serif;
}

.container{
  position: relative;
}

.phone-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; /* Occupy full width */
  max-width: 400px; /* Limit max width for larger screens */
  margin-bottom: 20px; /* Space between phone screen and app details */
}

.phone-frame-container {
  position: relative;
  width: 320px; /* Adjust width for phone frame */
}

.phone-frame {
  width: 100%;
  padding-top: 0px;
  height: auto;
  padding-top: 0px;
  margin-top: 0px;
  margin-right: 8vh;
  border: 1px solid #ccc;
  background-color: #cccccc3b;
  border-radius: 20px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.phone-screenshot {
  position: absolute;
    top: 4%;
    left: 4%;
    width: 8.8%;
    margin-top: 3.16vh;
    height: 32%;
    margin-left: 3.82vh;
    border-radius: 16px;
}
.phone-screen img{
  width: 43.4vh;
}
.phone-screen2 img{
  width: 31.4vh;
  height: 63vh;
}

.app-details {
  max-width: 70vh;
  display: flex; /* Adjust max width for app details */
  text-align: center;
  align-items: center;
  flex-direction: column;
}

.app-details h1 {
  padding-top: 0px;
  font-family: "League Spartan", sans-serif;
  font-size: 34px;
  font: bolder;
  color: rgb(45, 130, 12);
  text-align: left;
  margin-bottom: 30px;
}

.app-details p {
  font-size: 16px;
  line-height: 1.6;
  text-align: left;
  color: #666;
}

.buttons {
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  flex-wrap: wrap; /* Allow buttons to wrap on small screens */
  justify-content: center; 
}

button {
  margin: 5px; /* Adjust spacing between buttons */
  padding: 12px 24px;
  font-size: 16px;
  color: #fff;
  background-color: rgb(45, 130, 12);
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: rgb(45, 130, 12);
}

.button-icon {
  margin-right: 8px;
  vertical-align: middle;
}
.card {
  max-width: fit-content;
  border-radius: 15px;
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: center;
  gap: 0.6vh;
  backdrop-filter: blur(15px);
  box-shadow: inset 0 0 20px rgba(255, 255, 255, 0.192),
    inset 0 0 5px rgba(255, 255, 255, 0.274), 0 5px 5px rgba(0, 0, 0, 0.164);
  transition: 0.5s;
}

.card:hover {
  animation: ease-out 5s;
  background: rgba(173, 173, 173, 0.05);
}

.card ul {
  padding: 1rem;
  display: flex;
  list-style: none;
  gap: 1rem;
  align-items: center;
  justify-content: center;
  align-content: center;
  flex-wrap: wrap;
  flex-direction: row;
}

.card ul li {
  cursor: pointer;
}

.svg {
  transition: all 0.3s;
  /* if you find some problems change w - h : 30px*/
  padding: 1rem;
  height: 30px;
  width: 30px;
  border-radius: 100%;
  color: rgb(45, 130, 12);
  fill: currentColor;
  box-shadow: inset 0 0 20px rgba(255, 255, 255, 0.3),
    inset 0 0 5px rgba(255, 255, 255, 0.5), 0 5px 5px rgba(0, 0, 0, 0.164);
}

.text {
  opacity: 0;
  border-radius: 5px;
  padding: 5px;
  transition: all 0.3s;
  color: rgb(45, 130, 12);
  background-color: rgba(255, 255, 255, 0.3);
  position: absolute;
  z-index: 9999;
  box-shadow: -5px 0 1px rgba(153, 153, 153, 0.2),
    -10px 0 1px rgba(153, 153, 153, 0.2),
    inset 0 0 20px rgba(255, 255, 255, 0.3),
    inset 0 0 5px rgba(255, 255, 255, 0.5), 0 5px 5px rgba(0, 0, 0, 0.082);
}

/*isometric prooyection*/
.iso-pro {
  transition: 0.5s;
}
.iso-pro:hover a > .svg {
  transform: translate(15px, -15px);
  border-radius: 100%;
}

.iso-pro:hover .text {
  opacity: 1;
  transform: translate(25px, -2px) skew(-5deg);
}

.iso-pro:hover .svg {
  transform: translate(5px, -5px);
}

.iso-pro span {
  opacity: 0;
  position: absolute;
  color: #1877f2;
  border-color: #1877f2;
  box-shadow: inset 0 0 20px rgba(255, 255, 255, 0.3),
    inset 0 0 5px rgba(255, 255, 255, 0.5), 0 5px 5px rgba(0, 0, 0, 0.164);
  border-radius: 50%;
  transition: all 0.3s;
  height: 60px;
  width: 60px;
}

.iso-pro:hover span {
  opacity: 1;
}

.iso-pro:hover span:nth-child(1) {
  opacity: 0.2;
}

.iso-pro:hover span:nth-child(2) {
  opacity: 0.4;
  transform: translate(5px, -5px);
}

.iso-pro:hover span:nth-child(3) {
  opacity: 0.6;
  transform: translate(10px, -10px);
}

.button {
  height: 50px;
  width: 210px;
  position: relative;
  background-color: transparent;
  cursor: pointer;
  border: 2px solid rgb(45, 130, 12);
  overflow: hidden;
  border-radius: 30px;
  color: rgb(45, 130, 12);
  transition: all 0.5s ease-in-out;
}

.btn-txt {
  z-index: 1;
  font-weight: 800;
  letter-spacing: 4px;
}

.type1::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  transition: all 0.5s ease-in-out;
  background-color: #333;
  border-radius: 30px;
  visibility: hidden;
  height: 10px;
  width: 10px;
  z-index: -1;
}

.button:hover {
  box-shadow: 1px 1px 200px #252525;
  color: #fff;
  border: none;
}

.type1:hover::after {
  visibility: visible;
  transform: scale(100) translateX(2px);
}

